<template>
    <el-container>
        <el-main>
        <el-header>
            <Header></Header>
        </el-header>

        <el-card class="boxcard banner" shadow="never" :style="{backgroundImage:'url('+background+')'}">
        <div class="subtitle">
          <p class="name" >loster</p>
          <p class="saying">无穷的远方与无尽的人们，都与我有关</p>
        </div>

      <a href="#down" class="down"><p class="tip-arrow">  <i class=" el-icon-arrow-down animate__animated animate__bounce  animate__infinite "></i></p></a>
        </el-card>
        
        <el-row type="flex" align="top" class="boxcard content " shadow="never"  :style="{backgroundImage:'url('+require('../assets/bg2.jpg')+')'}">
          <p id="down"></p>

          <el-col :xs="23" :sm="23" :md="14" :lg="14" :xl="14"  style="margin-top:15px">
             <el-card class="essaybox"  shadow="never" >
               <br><br><br>
             </el-card>
          </el-col>

          <el-col class="hidden-sm-and-down" :md="5" :lg="5" :xl="5"  style="margin:15px 0 0 10px"> 
            <el-card class="info-box" shadow="never" :body-style="{padding:'0'}">

                <InfoBox></InfoBox>
               
              <br>
            </el-card>
            </el-col>
        </el-row>



          <BackTop></BackTop>
       
        </el-main>
        

        <el-footer>Footer</el-footer>
</el-container>
</template>


<script>
// 导入组件
import Header from '../components/Header.vue'
import InfoBox from '../components/InfoBox.vue'
import BackTop from '../components/BackTop.vue'
export default {
    name:'Home',
    components:{
        Header,
        InfoBox,
        BackTop,
    },
    data(){
      return {
          background:require('../assets/index.jpg')
      }
    },
    mounted(){
          
          // 页面滚动，导航栏效果
          window.addEventListener('scroll',function(){
              // 透明度变化
              const header = this.document.querySelector('.el-header')
              header.classList.toggle("opacityChange",this.scrollY > 0)
              
              // 导航栏的隐藏与显示
              let scrollTop = window.pageYOffset || document.documentElement.scrollTop || 
                        document.body.scrollTop
              let scroll = scrollTop - this.to;
              this.to = scrollTop;
              header.classList.toggle("hidden",!(scroll < 0))
             
        
          })
    }
}
</script>

<style lang="less" scoped>  
html,body{
  scroll-behavior:smooth;
}
  .el-header.hidden{
      transform: translate(0,-100%);
  }
  .back-top.hidden{
      transform: translate(-150%,0);
  }
  .el-header.opacityChange{
      background: rgba(172, 170, 170, 0.782);    
  }


  .el-header, .el-footer {
    background-color: #B3C0D1;
    color: #333;
    text-align: center;
    line-height: 60px;
  }
  .el-header{
      position:fixed;
      width: 100%;
      transition:all .4s;
      background-color: rgba(0, 0, 0, 0);
      z-index: 2;
  }
  .el-main {
    background-color: #ced0d1;
    color: #333;
    text-align: center;
    line-height: 160px;
    padding: 0 !important;
  }
  .boxcard{
      height: 100vh;
      background-repeat: no-repeat;
      background-attachment: fixed;
      background-size: cover;
      background-position: center;
      border: none !important;
      border-radius: 0 !important;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0 ;
      position: relative;
  }
  .content{
     display: flex;
     align-items:flex-start;
  }
  .subtitle{
    p{
      line-height:45px;
      color:white;
    }
    .name{
      font-size:3em;
      font-weight: 500;
    }
    .saying{
      font-size:1em;
      font-weight: 300;
    }
  }

  .info-box{
    padding: 0 !important;
  }

  .tip-arrow{
      color:rgb(210, 209, 209);
      line-height:45px;
      width: 20%;
      
      position: absolute;
      left: 50%;
      bottom:-5px;
      transform: translate(-50%,0);
      transition: all 1s;
      
  }
  .tip-arrow i{
    font-size:40px;    
    font-weight:1000;
    cursor: pointer;
  }
  
  .down{
  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
  -webkit-user-select: none;
  -moz-user-focus: none;
  -moz-user-select: none;
  }

    
</style>